/* Hamburger-icon*/
#hamburger-icon{
    width: 50px;
    display: flex;
    position: fixed;
    right: 5px;
    top: 30px;
    opacity: 0.5;
    border-radius: 10px;
    z-index: 100;
  }
  
  #menu-pop-up{
    display: flex;
    visibility: hidden;
    opacity: 0;
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: rgba(255, 255, 255, 1);
    top: 0;
    left: 0;
    overflow-y: scroll;
  }
  
  
  /* MENU*/
  .menu-container{
    background-color: rgba(222, 254, 254, 0.547);
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    margin-bottom: 40px;
    width: 100%;
    max-width: 600px;
    padding-bottom: 10px;
    flex:1;
    border: 1px solid rgb(206, 206, 206);
  }
  .menu-top{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 22px;
    padding: 24px 0px;
    border-bottom: 1px solid rgb(206, 206, 206); 
  }
  
  .menu-bottom{
    padding: 10px 10px;
    margin: 0px 10px;
    display:flex;
    line-height: 35px;
    flex-direction: column;
    align-items: center;
    vertical-align: text-top;
    font-size: 15px;
    
  }
  
  .aktualita{
    padding: 0;
    padding-top: 10px;
  }
  
  .menu-bottom-media{
    padding: 10px 10px;
    margin: 0px 10px;
    display:flex;
    flex-direction: column;
    line-height: 35px;
    text-align: center;
    font-size: 15px;
  }
  
  .menu-bottom-follow{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 20px;
  }
  
  .menu-icon-container{
    width: 35px;
    height: 35px;
    margin-right: 20px;
    background-color: white;
    border-radius: 40px;
  }
  
  .menu-icon{
    width: 35px;
    height: 35px;
    object-fit: cover;
    border-radius: 20px;
    cursor: pointer;
  }
  
  .menu-icon:hover{
    opacity: 0.7;
  }
  
  .menu-item{
    line-height: 25px;
    padding-top: 6px;
    cursor: pointer;
    color: darkblue;
  }
  
  /*
  .menu-index{
    border: none;
  }
  */
  
  .table-item{
    line-height: 25px;
    padding-bottom: 12px;
    color: black;
    
  }
  
  .menu-item:hover{
    color: rgb(207, 10, 10);
    text-decoration: underline;
  }
  
  .aktualni-sbirka{
    display: flex; 
    flex-direction: column; 
    justify-content: center; 
    position: relative; 
    width: 100%;
    background-color: #250d61; 
    height: 200px;
    z-index: 0;
  }
  
  .aktualni-sbirka:hover{
    background-color: #381492;
  }
  
  .qr-code-kb{
    max-width: 50%;
  }
  
  @media (max-width:1100px){
    .menu-top{
      font-size: 18px;
    }
  }
  
  .nav-article{
    padding-top: 47px;
  }
  
  @media (min-width:700px){
    #hamburger-menu{
      display: none
    }
  }
  
  
